.loaderContainer {
    height:100%;
    width:100%;
display: grid;
justify-content: center;
align-items: center;
}

.canvas {

    align-items: center;
    background: #eeeeee00;
    border-radius: 50%;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
    display: flex;
    height: 10em;
    justify-content: center;
    margin: 1em 1em 2em 1em;
    width: 10em;
  }
  
 
  .spinner1 {
    align-items: center;
    border: .3em solid transparent;
    border-top: .3em solid orange;
    border-right: .3em solid orange;
    border-radius: 100%;
    display: flex;
    justify-content: center;
  }
  
  .spinnerMax {
    animation: spinnerOne 3s linear infinite;
    height: 3em;
    width: 3em;
  }
  
  .spinnerMid {
    animation: spinnerOne 5s linear infinite;
    height: 2.4em;
    width: 2.4em;
  }
  
  .spinnerMin {
    animation: spinnerOne 5s linear infinite;
    height: 1.8em;
    width: 1.8em;
  }
  
  @keyframes spinnerOne {
    0% {
      transform: rotate(0deg)
    }
    100% {
      transform: rotate(360deg)
    }
  }